  /**
   * @param {Event} event - Click event.
   */
  function doBulkAction(event) {
    var targetElement = $(event.target);
    var action = targetElement.prev('select').val();
    var spinnerWrapper = targetElement.parent('.bulkactions');

    /**
     * Check the selected action is ours.
     */
    if (!Object.values(wcmp.bulk_actions).includes(action)) {
      return;
    }
    event.preventDefault();
    removeNotices();
    var checkedRowsSelector = '#the-list th.check-column input[type="checkbox"]:checked';
    var checkedRows = _toConsumableArray(document.querySelectorAll(checkedRowsSelector));
    var orderIds = checkedRows.map(function (element) {
      return element.value;
    });
    var rows = orderIds.map(function (id) {
      return ".post-".concat(id);
    });
    $(rows.join(', ')).addClass('wcmp__loading');
    if (!orderIds.length) {
      showAdminNotice(wcmp.strings.no_orders_selected);
      return;
    }
    switch (action) {
      // Export orders.
      case wcmp.bulk_actions.export:
        exportToMyParcel.bind(spinnerWrapper)(orderIds);
        break;

      // Print labels.
      case wcmp.bulk_actions.print:
        printLabel.bind(spinnerWrapper)({
          order_ids: orderIds
        });
        break;

      // Export and print.
      case wcmp.bulk_actions.export_print:
        exportToMyParcel.bind(spinnerWrapper)(orderIds, 'yes');
        break;
    }
  }

